.sing-in-form-backdrop, 
.sing-up-form-backdrop {
    position: fixed;
    left: 0;
    top: 0;
    width: 100vw;
    height: 100vh;
    background: rgba(0, 0, 0, 0.2);
    z-index: 15;
    transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);

&.is-hidden {
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
     }
}

.sing-in-modal,
.sing-up-modal {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) scale(1);
    width: 100vw;
    max-width: 450px;
    background: #ffffff;
    box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14),
        0px 2px 1px rgba(0, 0, 0, 0.2);
    border-radius: 4px;
    padding: 40px;
    
    .backdrop.is-hidden & {
         transform: scale(1.1);
    }
}

.btn {
    min-width: 200px;
    padding: 10px 32px;
    border: none;
    border-radius: 4px;
    font-weight: 500;
    font-size: 12px;
    line-height: 14px;
    text-align: center;
    align-items: center;
    letter-spacing: 0.06em;
    cursor: pointer;
    color: #ffffff;
    background-color: var(--accent-color);
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
    transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1),
    box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1), color 250ms cubic-bezier(0.4, 0, 0.2, 1);
    
    &:hover,
    &:focus {
    color: var(--accent-color);
    background-color: #ffffff;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
    }
}

  .sing-in-modal__btn-close,
  .sing-up-modal__btn-close {
    position: absolute;
    right: 8px;
    top: 8px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 30px;
    height: 30px;
    border-radius: 50%;
    background: #ffffff;
    border: 1px solid rgba(0, 0, 0, 0.1);
    padding: 0;
    fill: var(--main-text);
    cursor: pointer;
    transition: fill 250ms cubic-bezier(0.4, 0, 0.2, 1);
    
    &:hover,
    &:focus {
        fill: var(--accent-color);
    }
}


.sing-in-form__label,
.sing-up-form__label {
    display: block;
}

.sing-in-form__input,
.sing-up-form__input {
    padding-left: 40px;
    padding-right: 0;
    width: 100%;
    height: 40px;
    border: 1px solid rgba(33, 33, 33, 0.2);
    border-radius: 4px;
}

.sing-in-form__btn,
.sing-up-form__btn {
    display: block;
    margin: 0 auto;
    outline: none;
    padding: 10px 55px;
    margin-top: 50px;
}
    
     // desktop +
@media screen and (min-width: $desktop) {
    .sing-in-modal,
    .sing-up-modal  {
    width: 528px;
    height: 581px;
    }
}   