.light-theme {
  background-color: var(--light-theme-background);
  color: var(--main-text);
}

.dark-theme {
  background-color: var(--dark-theme-background);
  color: var(--white-text);

  .library-empty__tumb {
    background-image: url('../images/library/tablet/library-empty-invert-704.jpg');

    @media (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: url('../images/library/tablet/library-empty-704@2x.jpg');
    }
  }
}

.grey-background-theme {
  box-shadow: 0 0 7px var(--footer-shadow);
  background-color: var(--grey-theme-background);

  .footer__desc {
    color: var(--white-text);
  }
}

.toolbar {
  display: flex;
  justify-content: flex-end;
}

.toolbar-library {
  display: flex;
  justify-content: flex-end;
  margin-top: 55px;
  padding-right: 80px;
}

.theme-switch {
  display: flex;
  position: absolute;
  bottom: 10%;
  right: 50%;
  transform: translate(50%, 10%);
}
.theme-switch-icon {
  fill: var(--accent-color);
  color: currentColor;
  height: 20px;
  width: 15px;
  margin-right: 5px;
  margin-left: 5px;
}

.theme-switch-control {
  display: flex;
  align-items: center;
  flex-shrink: 0;
  position: relative;
  width: 60px;
  height: 20px;
  border-radius: 50em;
  padding: 3px 0;
}

.theme-switch-toggle {
  position: absolute;
  left: 0;
  top: 0;
  margin: 0;
  padding: 0;
  opacity: 0;
  height: 0;
  width: 0;
  pointer-events: none;
}

.theme-switch-track {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  color: transparent;
  user-select: none;
  background-color: #333;
  border-radius: inherit;
  z-index: 1;
  cursor: pointer;
  box-shadow: 0 0 0 2px var(--accent-color);
}

.theme-switch-marker {
  position: relative;
  background-color: var(--light-theme-background);
  width: 20px;
  height: 20px;
  border-radius: 50%;
  z-index: 2;
  pointer-events: none;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.25);
  transform: translateX(3px);
  transition: 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.theme-switch-toggle:checked + .theme-switch-track {
  background-color: #d3d3d4;
}

.theme-switch-toggle:checked ~ .theme-switch-marker {
  transform: translateX(35px);
}
